/* Reset */
* { margin: 0; padding: 0; box-sizing: border-box; }
body { font-family: Arial, sans-serif; background: #0d1117; color: #e6edf3; overflow-x: hidden; line-height: 1.6; }
h1,h2 { color: #fff; text-align: center; margin-bottom: 1rem; }
.highlight { color: #58a6ff; }

/* Background glowing lines */
.background-lines {
  position: fixed;
  inset: 0;
  background: radial-gradient(circle, rgba(88,166,255,0.05) 1px, transparent 1px);
  background-size: 40px 40px;
  animation: moveGrid 20s linear infinite;
  z-index: -1;
}
@keyframes moveGrid {
  from { background-position: 0 0; }
  to { background-position: 40px 40px; }
}

/* Hero */
.hero { min-height: 100vh; display: flex; flex-direction: column; justify-content: center; align-items: center; gap: 1.5rem; text-align: center; }
.hero h1 { font-size: 2.5rem; }
.hero h2 { font-size: 1.5rem; }
.hero-logos { display: flex; flex-wrap: wrap; justify-content: center; gap: 1rem; max-width: 600px; }
.hero-logos img { width: 60px; height: 60px; object-fit: contain; filter: drop-shadow(0 0 6px rgba(88,166,255,0.6)); }

/* Sections */
section { padding: 4rem 2rem; max-width: 1000px; margin: auto; }
.grid { display: grid; grid-template-columns: repeat(auto-fit, minmax(200px,1fr)); gap: 1.5rem; }
.card { background: #161b22; padding: 1rem; border-radius: 12px; text-align: center; transition: transform 0.3s; }
.card:hover { transform: translateY(-5px); }
.card img { width: 80px; height: 80px; object-fit: contain; margin-bottom: 0.5rem; }

/* Reveal on scroll */
.reveal { opacity: 0; transform: translateY(40px); transition: all 0.8s ease-out; }
.reveal.visible { opacity: 1; transform: translateY(0); }

/* Contact */
.contact { text-align: center; }

/* Footer */
footer { padding: 2rem; text-align: center; background: #161b22; margin-top: 2rem; }
